
import { mapGetters, mapActions, mapMutations } from "vuex";
import { api } from "@/utils/api.js";
import loading from "@/components/loading.vue";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

export default {
name: "subStoreList",
props: {},
components: {
Group,
Cell,
XButton,
Badge,
XHeader,
loading
},
data() {
return {
storeList: [],
store_list_page: 1,
fanShow_bottom: false,
loading_bottom: false,
selectModelStatus: false,
img1: false,
img1_sn: "",
shopStr: "",
shopOwnerStr: "",
shopTel: "",
subsaleslist:[]
};
},
created() {
this.getStoreList();
},
methods: {

async getStoreList(){
let data={
plat: 3,
account: this.account,
token: this.token
};
const [err, res] = await api.offlineStoreList(data);
if (err) {
console.log("err", err);
return;
}
if(res && res.code=="2000"){
this.subsaleslist=res.data.list;
console.log('this.subsaleslist',this.subsaleslist)
}
},

orderLink(e){
var subOrderId=e.target.dataset.submemberid;
this.$router.push({
path:'/offlineStoreOrder',
name:'offlineStoreOrder',
params:{
subOrderId:subOrderId
}
});
},
applyOrderLink(e){
var subOrderId=e.target.dataset.submemberid;
console.log(subOrderId)
this.$router.push({//跳路由
path: '/drawIndex',
query: {
sub_member_id:subOrderId
}
});
}

},
filters: {},
computed: {
...mapGetters(["user", "account", "token"])
},
};



